// src/app/components/almacen/ReporteAlmacen.tsx
"use client";

import { useMemo } from "react";
import { Download, BarChart2 } from "lucide-react";

interface Movimiento {
  producto: string;
  cantidad: number;
  tipo: "entrada" | "salida";
  fecha: string;
}

export default function ReporteAlmacen() {
  const movimientos: Movimiento[] = JSON.parse(
    typeof window !== "undefined"
      ? localStorage.getItem("movimientos") || "[]"
      : "[]"
  );

  const totalEntradas = useMemo(
    () => movimientos.filter((m) => m.tipo === "entrada").reduce((acc, m) => acc + m.cantidad, 0),
    [movimientos]
  );

  const totalSalidas = useMemo(
    () => movimientos.filter((m) => m.tipo === "salida").reduce((acc, m) => acc + m.cantidad, 0),
    [movimientos]
  );

  const productoMasMovido = useMemo(() => {
    const conteo: Record<string, number> = {};
    movimientos.forEach((m) => {
      conteo[m.producto] = (conteo[m.producto] || 0) + m.cantidad;
    });
    return Object.entries(conteo).sort((a, b) => b[1] - a[1])[0]?.[0] || "N/A";
  }, [movimientos]);

  const exportarCSV = () => {
    const contenido = `Métrica,Valor
Total de Entradas,${totalEntradas}
Total de Salidas,${totalSalidas}
Producto más movido,${productoMasMovido}`;
    const blob = new Blob([contenido], { type: "text/csv" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "reporte_almacen.csv";
    a.click();
    URL.revokeObjectURL(url);
  };

  return (
    <section>
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-xl font-semibold flex items-center gap-2">
          <BarChart2 size={20} /> Reporte de Almacén
        </h2>
        <button
          onClick={exportarCSV}
          className="flex items-center gap-2 bg-indigo-600 text-white px-4 py-2 rounded-md"
        >
          <Download size={16} /> Exportar CSV
        </button>
      </div>

      <div className="grid gap-4 grid-cols-1 sm:grid-cols-3">
        <div className="bg-green-100 p-4 rounded-md shadow-sm">
          <p className="text-sm text-gray-700">Total Entradas</p>
          <h3 className="text-xl font-bold text-green-700">{totalEntradas}</h3>
        </div>
        <div className="bg-red-100 p-4 rounded-md shadow-sm">
          <p className="text-sm text-gray-700">Total Salidas</p>
          <h3 className="text-xl font-bold text-red-700">{totalSalidas}</h3>
        </div>
        <div className="bg-blue-100 p-4 rounded-md shadow-sm">
          <p className="text-sm text-gray-700">Producto más movido</p>
          <h3 className="text-xl font-bold text-blue-700">{productoMasMovido}</h3>
        </div>
      </div>
    </section>
  );
}
